import { BrowserRouter as Rauter, Switch, Route } from 'react-router-dom'
import Detail from './routes/Detail';
import Home from './routes/Home';

function App() {
  return <Rauter>
    <Switch>
      <Route path="/movie/:id">
        <Detail />
      </Route>
      <Route path="/">
        <Home />
      </Route>
    </Switch>
  </Rauter>;
}

export default App;
